body.rtl {
    text-align: right;
    direction: rtl;
}

.rtl {
    dd {
        margin-right: 0;
        margin-left: inherit;
    }

    blockquote {
        margin: 0 0 1rem;
    }

    figure {
        margin: 0 0 1rem;
    }

    caption {
        text-align: right;
    }

    .list-unstyled,
    .list-inline {
        padding-right: 0;
        padding-left: inherit;
    }

    .list-inline-item:not(:last-child) {
        margin-left: 0.5rem;
        margin-right: inherit;
    }

    .container,
    .container-fluid {
        padding-left: 15px;
        padding-right: 15px;
        margin-left: auto;
        margin-right: auto;
    }

    .row {
        margin-left: -15px;
        margin-right: -15px;
    }

    .no-gutters {
        margin-left: 0;
        margin-right: 0;
    }

    .no-gutters > .col,
    .no-gutters > [class*="col-"] {
        padding-left: 0;
        padding-right: 0;
    }

    /* ###### Accordion ###### */

    .accordion > .card:first-of-type {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }

    .accordion > .card:last-of-type {
        border-top-right-radius: 0;
        border-top-left-radius: 0;
    }

    .main-accordion .accordion-body {
        padding: 0 20px 20px;
    }

    /* ###### Accordion ###### */

    /* ###### Alerts ###### */

    .alert .btn-close {
        left: 15px;
        right: inherit !important;
    }

    .alert-dismissible {
        padding-left: 3.8125rem;
        padding-right: 1.25rem;
    }
    .alert i{
        float: right;
        margin-top: 5px;
    }
    .alert-dismissible .btn-close {
        left: 15px;
        right: inherit !important;
    }

    .alert-bdleft-success {
        border-right: 5px solid #19b159;
        border-left: 1px solid rgba(25, 177, 89, 0.1);
    }

    .alert-bdleft-primary {
        border-right: 5px solid $primary;
        border-left: 1px solid $primary-transparent;
    }

    .alert-bdleft-warning {
        border-right: 5px solid #ff9b21;
        border-left: 1px solid rgba(255, 155, 33, 0.1);
    }

    .alert-bdleft-danger {
        border-right: 5px solid #fd6074;
        border-left: 1px solid rgba(253, 96, 116, 0.1);
    }

    .alert-bdleft-info {
        border-right: 5px solid #01b8ff;
        border-left: 1px solid rgba(1, 184, 255, 0.1);
    }

    .alert-bdleft-secondary {
        border-right: 5px solid #f1388b;
        border-left: 1px solid rgba(241, 56, 139, 0.1);
    }

    /* ###### Alerts ###### */

    /* ###### Badge ###### */
    .badge {
        padding: 5px 6px 4px 6px;
    }

    .rounded-pill {
        padding-right: 8px;
        padding-left: 8px;
        padding-left: 0.6em;
        padding-right: 0.6em;
    }

    /* ###### Badge ###### */

    /* ###### Breadcrumb ###### */

    .main-breadcrumbs .breadcrumb-item + .breadcrumb-item {
        padding-right: 8px;
        padding-left: inherit;
    }

    .main-breadcrumbs .breadcrumb-item + .breadcrumb-item::before {
        padding-left: 8px;
        padding-right: inherit;
    }

    .breadcrumb-item + .breadcrumb-item {
        padding-right: 0.5rem;
        padding-left: inherit;
    }

    .breadcrumb-item + .breadcrumb-item::before {
        padding-left: 0.3rem;
        padding-right: 0.3rem;
    }

    .breadcrumb-style2 .breadcrumb-item + .breadcrumb-item::before {
        content: "\f3cf";
    }

    .breadcrumb-5 a {
        float: right;
        padding: 0 39px 0 10px;
    }

    .breadcrumb-5 a:first-child {
        padding-right: 46px;
        padding-left: inherit;
        border-radius: 0 5px 5px 0;
    }

    .breadcrumb-5 a:first-child:before {
        right: 14px;
        left: inherit !important;
    }

    .breadcrumb-5 a:last-child {
        border-radius: 5px 0 0 5px;
    }

    .breadcrumb-5 a:after {
        left: -18px;
        right: inherit !important;
        border-radius: 5px 0 50px 0;
    }

    .breadcrumb-4 ol {
        text-align: left;
    }

    .breadcrumb-4 li {
        text-align: left;
    }

    /* ###### Breadcrumb ###### */

    /* ###### Buttons ###### */

    .btn-group > .btn:not(:first-child),
    .sp-container .btn-group > button:not(:first-child) {
        margin-right: -1px;
        margin-left: initial;
    }

    .btn-group > .btn-group:not(:first-child) {
        margin-right: -1px;
        margin-left: inherit;
    }

    .btn-group > .btn:not(:last-child):not(.dropdown-toggle) {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }
    .btn-group > .btn:first-child {
        border-top-right-radius: 6px !important;
        border-bottom-right-radius: 6px !important;
    }
    .sp-container .btn-group > button:not(:last-child):not(.dropdown-toggle),
    .btn-group > .btn-group:not(:last-child) > .btn,
    .sp-container .btn-group > .btn-group:not(:last-child) > button {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }

    .btn-sm + .dropdown-toggle-split,
    .btn-group-sm > .btn + .dropdown-toggle-split,
    .sp-container .btn-group-sm > button + .dropdown-toggle-split {
        padding-left: 0.375rem;
        padding-right: 0.375rem;
    }

    .btn-lg + .dropdown-toggle-split,
    .btn-group-lg > .btn + .dropdown-toggle-split,
    .sp-container .btn-group-lg > button + .dropdown-toggle-split {
        padding-left: 0.75rem;
        padding-right: 0.75rem;
    }

    .btn-group-vertical > .btn:not(:last-child):not(.dropdown-toggle) {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }

    .sp-container .btn-group-vertical > button:not(:last-child):not(.dropdown-toggle),
    .btn-group-vertical > .btn-group:not(:last-child) > .btn,
    .sp-container .btn-group-vertical > .btn-group:not(:last-child) > button {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }

    .btn-group-vertical > .btn:not(:first-child),
    .sp-container .btn-group-vertical > button:not(:first-child),
    .btn-group-vertical > .btn-group:not(:first-child) > .btn,
    .sp-container .btn-group-vertical > .btn-group:not(:first-child) > button {
        border-top-right-radius: 0;
        border-top-left-radius: 0;
    }

    .btn-with-icon i {
        margin-left: 5px;
        margin-right: inherit;
    }

    .btn-icon-list .btn + .btn,
    .btn-icon-list .sp-container button + .btn {
        margin-right: 5px;
        margin-left: inherit;
    }

    .sp-container .btn-icon-list button + .btn,
    .btn-icon-list .sp-container .btn + button,
    .sp-container .btn-icon-list .btn + button,
    .btn-icon-list .sp-container button + button,
    .sp-container .btn-icon-list button + button {
        margin-right: 5px;
        margin-left: inherit;
    }

    .btn-list > .btn:not(:last-child),
    .btn-list > .dropdown:not(:last-child) {
        margin-left: 0.5rem;
        margin-right: inherit;
    }

    .btn-animation .btn-loaders {
        padding-left: 46px;
        padding-right: 0.95rem;
    }

    .btn-animation .btn-loaders:after {
        border-right-color: transparent !important;
        left: 13px !important;
        right: inherit !important;
    }

    /* ###### Buttons ###### */

    /* ###### Card  ###### */

    .card > hr {
        margin-left: 0;
        margin-right: 0;
    }

    .card > .list-group:first-child .list-group-item:first-child {
        border-top-right-radius: 3px;
        border-top-left-radius: 3px;
    }

    .card > .list-group:last-child .list-group-item:last-child {
        border-bottom-left-radius: 3px;
        border-bottom-right-radius: 3px;
    }

    // .card-link+.card-link {
    //     margin-right: 1.25rem;
    //     margin-left: inherit;
    // }

    .card-header {
        padding: 20px 25px 0 25px;
    }

    .card-header:first-child {
        border-radius: calc(3px - 1px) calc(3px - 1px) 0 0;
        border-radius: 11px 11px 0 0;
    }

    .card-footer:last-child {
        border-radius: 0 0 11px 11px;
    }

    .card-header-tabs {
        margin-left: -0.625rem;
        margin-right: -0.625rem;
    }

    .card-header-pills {
        margin-left: -0.625rem;
        margin-right: -0.625rem;
    }

    .card-img-overlay {
        left: 0;
        right: 0;
    }

    .card-img-top {
        border-top-right-radius: 11px;
        border-top-left-radius: 11px;
    }

    .card-img-bottom {
        border-bottom-left-radius: 11px;
        border-bottom-right-radius: 11px;
    }

    @media (min-width: 576px) {
        .card-deck {
            margin-left: -15px;
            margin-right: -15px;
        }

        .card-deck .card {
            margin-left: 15px;
            margin-right: 15px;
        }
    }

    @media (min-width: 576px) {
        .card-group > .card + .card {
            margin-right: 0;
            margin-left: inherit;
            border-right: 0;
            border-left: inherit !important;
        }

        .card-group > .card:not(:last-child) {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }

        .card-group > .card:not(:last-child) .card-img-top,
        .card-group > .card:not(:last-child) .card-header {
            border-top-left-radius: 0;
        }

        .card-group > .card:not(:last-child) .card-img-bottom,
        .card-group > .card:not(:last-child) .card-footer {
            border-bottom-left-radius: 0;
        }

        .card-group > .card:not(:first-child) {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }

        .card-group > .card:not(:first-child) .card-img-top,
        .card-group > .card:not(:first-child) .card-header {
            border-top-right-radius: 0;
        }

        .card-group > .card:not(:first-child) .card-img-bottom,
        .card-group > .card:not(:first-child) .card-footer {
            border-bottom-right-radius: 0;
        }
    }

    .card-header,
    .card-footer {
        padding-right: 25px;
        padding-left: 25px;
    }

    .card-item .card-item-icon {
        left: 20px;
        right: inherit !important;
    }

    .card-dashboard-twentyfour .card-header {
        padding: 20px 20px 0;
    }

    .card-dashboard-twentyfour .card-body {
        padding: 15px 20px 20px;
    }

    .card-dashboard-twentyfour .card-body-top > div + div {
        margin-right: 30px;
        margin-left: inherit;
    }

    .card-dashboard-twentyfour .chart-legend span {
        margin-left: 5px;
        margin-right: inherit;
    }

    .card-dashboard-twentyfour .chart-legend > div + div {
        margin-right: 20px;
        margin-left: inherit;
    }

    .card-dashboard-twentyfour .chart-wrapper {
        margin-left: -10px;
        margin-right: inherit;
    }

    .card-dashboard-twentyfive .col + .col {
        border-right: 1px solid $border;
        border-left: inherit !important;
    }

    .card-dashboard-twentyfive .chart-wrapper {
        margin: 0 -8px -5px;
    }

    .card-dashboard-twentysix .card-header {
        padding: 15px 15px 10px;
    }

    .card-dashboard-twentysix .chart-legend > div + div {
        margin-right: 15px;
        margin-left: inherit;
    }

    .card-dashboard-twentysix .chart-legend span {
        margin-left: 2px;
        margin-right: inherit;
    }

    .card-dashboard-twentysix .chart-wrapper {
        margin: -30px -18px -15px -18px;
    }

    .card-table .badge {
        padding: 9px 13px 9px;
    }

    .card-options {
        margin-right: auto;
        margin-left: -0.5rem;
    }

    .card-options a:not(.btn) {
        margin-right: 0;
        margin-left: inherit;
    }

    .card-fullscreen {
        right: 0;
        left: 0;
    }

    .card-blog-overlay:before {
        left: 0;
        right: inherit !important;
    }

    .card-blog-overlay1:before,
    .card-blog-overlay2:before {
        left: 0;
        right: inherit !important;
    }

    @media (max-width: 575px) {
        .card-box img {
            right: -14px !important;
            left: inherit !important;
        }
    }

    /* ###### Card  ###### */

    /* ###### carousel ###### */

    .carousel-item {
        float: right;
        margin-left: -100%;
        margin-right: inherit;
    }

    .carousel-control-prev {
        right: 0;
        left: inherit !important;
    }

    .carousel-control-next {
        left: 0;
        right: inherit !important;
    }

    .carousel-indicators {
        padding-right: 0;
        padding-left: inherit;
        margin-left: 15%;
        margin-right: 15%;
    }

    .carousel-indicators li {
        margin-left: 3px;
        margin-right: 3px;
    }

    .carousel-caption {
        left: 15%;
        right: 15%;
    }

    .carousel-slider #thumbcarousel {
        margin: 10px 0 0 0;
    }

    #thumbcarousel .carousel-control-prev {
        right: 0;
        left: inherit !important;
    }

    #thumbcarousel .carousel-control-next {
        left: 0;
        right: inherit !important;
    }

    .owl-nav .owl-prev {
        right: -25px;
        left: -1.5em;
    }

    .owl-nav .owl-next {
        left: -25px;
        right: inherit !important;
    }

    .owl-nav button:before {
        right: 0;
        left: 0;
    }

    .owl-theme .owl-dots {
        right: 0;
        left: 0;
    }

    /* ###### carousel ###### */

    /* ########## Dropdown ########## */

    .dropdown-toggle:empty::after {
        margin-right: 0;
        margin-left: inherit;
    }

    .dropdown-menu-left {
        left: auto;
        right: 0;
    }

    .dropdown-menu-right {
        left: 0;
        right: auto;
    }

    @media (min-width: 576px) {
        .dropdown-menu-sm-left {
            left: auto;
            right: 0;
        }

        .dropdown-menu-sm-right {
            left: 0;
            right: auto;
        }
    }

    @media (min-width: 768px) {
        .dropdown-menu-md-left {
            left: auto;
            right: 0;
        }

        .dropdown-menu-md-right {
            left: 0;
            right: auto;
        }
    }

    @media (min-width: 992px) {
        .dropdown-menu-lg-left {
            left: auto;
            right: 0;
        }

        .dropdown-menu-lg-right {
            left: 0;
            right: auto;
        }
    }

    @media (min-width: 1200px) {
        .dropdown-menu-xl-left {
            left: auto;
            right: 0;
        }

        .dropdown-menu-xl-right {
            left: 0;
            right: auto;
        }
    }

    .dropup .dropdown-toggle::after {
        margin-right: 0.255em;
        margin-left: inherit;
        border-left: 0.3em solid transparent;
        border-right: 0.3em solid transparent;
    }

    .dropup .dropdown-toggle:empty::after {
        margin-right: 0;
        margin-left: inherit;
    }

    .dropend .dropdown-menu {
        left: auto;
        right: 100%;
        margin-right: 0.125rem;
        margin-left: inherit;
    }

    .dropend .dropdown-toggle::after {
        margin-right: 0.255em;
        margin-left: inherit;
        border-left: 0;
        border-right: 0.3em solid;
    }

    .dropend .dropdown-toggle:empty::after {
        margin-right: 0;
        margin-left: inherit;
    }

    .dropstart .dropdown-menu {
        left: 100%;
        right: auto;
        margin-left: 0.125rem;
        margin-right: inherit;
    }

    .dropstart .dropdown-toggle::after {
        margin-right: 0.255em;
        margin-left: inherit;
    }

    .dropstart .dropdown-toggle::before {
        margin-left: 0.255em;
        margin-right: inherit;
        border-left: 0.3em solid;
        border-right: inherit !important;
    }

    .dropstart .dropdown-toggle:empty::after {
        margin-right: 0;
        margin-left: inherit;
    }

    .dropdown-header {
        padding-right: 15px;
        padding-left: 15px;
    }

    .dropdown-menu .main-header-center .select2-container--default .select2-selection--single {
        border-radius: 0 6px 6px 0;
    }

    .dropdown-menu .main-header-center .search-btn {
        border-radius: 6px 0 0 6px;
    }

    .dropdown-menu .main-form-search .btn,
    .dropdown-menu .main-form-search .sp-container button {
        padding: 0.375rem 0.7rem 0.4rem 0.85rem;
        border-radius: 6px 0 0 6px;
    }

    .dropdown-menu .sp-container .main-form-search button {
        padding: 0.375rem 0.7rem 0.4rem 0.85rem;
        border-radius: 6px 0 0 6px;
    }

    .dropdown-toggle-split {
        padding-left: 0.5625rem;
        padding-right: 0.5625rem;
    }

    .dropdown-toggle-split::after {
        margin-right: 0;
        margin-left: inherit;
    }

    .dropup .dropdown-toggle-split::after,
    .dropend .dropdown-toggle-split::after {
        margin-right: 0;
        margin-left: inherit;
    }

    .dropstart .dropdown-toggle-split::before {
        margin-left: 0;
        margin-right: inherit;
    }

    .dropdown.header-message .nav-link.icon {
        font-size: 18px;
    }

    .dropdown-menu {
        float: right;
        // margin: 0.125rem 0 0 !important;
        text-align: right;
    }

    .dropdown-menu[x-placement^="top"],
    .dropdown-menu[x-placement^="right"],
    .dropdown-menu[x-placement^="bottom"],
    .dropdown-menu[x-placement^="left"] {
        left: auto;
        right: inherit !important;
    }

