    /* ###### Form-elements ###### */

    .form-control-plaintext.form-control-sm,
    .form-control-plaintext.form-control-lg {
        padding-left: 0;
        padding-right: 0;
    }

    .form-row {
        margin-left: -5px;
        margin-right: -5px;
    }

    .form-row > .col,
    .form-row > [class*="col-"] {
        padding-left: 5px;
        padding-right: 5px;
    }

    .form-check {
        padding-right: 1.25rem;
        padding-left: inherit;
    }

    .form-check-input {
        margin-right: -1.25rem;
        margin-left: inherit;
    }

    .form-check-inline {
